.vioce_box {
  box-sizing: border-box;
  margin: 0 auto;
  width: 45vw;
  .style_box {
    display: none;
  }

  & .highlight_text {
    &::after {
      content: ' ';
      background-image: url('data:image/svg+xml;base64,');
      background-repeat: no-repeat;
    }
  }

